import { cva } from 'cva'
import type { ClassRecord } from '@/shared/lib/style'
import type { Size } from './types'

type SizeRecord = ClassRecord<Size>

export const avatar = cva({
  base: 'inline-block ring-2 ring-white',
  variants: {
    size: {
      xs: 'size-6',
      sm: 'size-8',
      base: 'size-10',
      lg: 'size-12',
      xl: 'size-14',
    } satisfies SizeRecord,
    square: {
      false: 'rounded-full',
      true: 'rounded-md',
    },
  },
})

export const avatarInitials = cva({
  base: 'flex select-none items-center justify-center bg-gray-500 font-medium leading-none text-white',
  variants: {
    size: {
      xs: 'text-xs',
      sm: 'text-sm',
      base: 'text-base',
      lg: 'text-lg',
      xl: 'text-xl',
    } satisfies SizeRecord,
  },
})

export const nameToInitials = (name: string) => (
  name
    .split(/[\s-]+/)
    .map((word) => word.substring(0, 1))
    .join('')
    .substring(0, 2)
    .toUpperCase()
)
