<script setup lang="ts">
import { type HTMLAttributes, computed, toRefs } from 'vue'
import { UserIcon } from '@heroicons/vue/24/solid'

import { type ClassValue, cn } from '@/shared/lib/style'

import type { Size } from './types'
import { avatar, avatarInitials, nameToInitials } from './lib'

export interface Props extends /* @vue-ignore */ HTMLAttributes {
  class?: ClassValue
  rootClass?: ClassValue
  src?: string | null
  size?: Size
  placeholderName?: string
  square?: boolean
}

const props = withDefaults(defineProps<Props>(), { size: 'base' })

export interface Slots {
  default?: () => any
  description?: () => any
}

const $slots = defineSlots<Slots>()

const { src, placeholderName } = toRefs(props)

const initials = computed(() => {
  if (!src.value && placeholderName.value) {
    return nameToInitials(placeholderName.value)
  }

  return null
})
</script>

<template>
  <div
    :class="cn(
      $slots.default || $slots.description
        ? 'group flex items-center gap-3'
        : '',
      props.rootClass ?? '',
    )
    "
  >
    <img
      v-if="src"
      aria-label="image"
      :class="cn(avatar({ size, square }), props.class ?? '')"
      :src="src"
      :alt="placeholderName"
    >
    <div
      v-else-if="!placeholderName"
      aria-label="placeholder"
    >
      <UserIcon
        class="bg-gray-100 text-gray-300"
        :class="cn(avatar({ size, square }), props.class ?? '')"
      />
    </div>

    <div
      v-else-if="placeholderName"
      aria-label="initials"
      :class="cn(
        avatar({ size, square }),
        avatarInitials ({ size }),
        props.class ?? '',
      )"
    >
      {{ initials }}
    </div>

    <div
      v-if="$slots.default || $slots.description"
    >
      <div
        v-if="$slots.default"
        class="
        text-sm font-medium text-gray-700 transition duration-1000
        group-hover:text-gray-900
        "
      >
        <slot />
      </div>

      <div
        v-if="$slots.description"
        class="text-xs font-medium text-gray-500 group-hover:text-gray-700"
      >
        <slot name="description" />
      </div>
    </div>
  </div>
</template>
